/*
=====
LODEARS
=====
*/
html, body {
    overflow-y: hidden;
    overflow-x: hidden;
}


a {
    text-decoration: none;
    &:hover, &:focus {
        text-decoration: none;
    }
}

.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #b39861;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      -webkit-transform: rotateY(180deg);
              transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0;
        transform: scale(0.987) translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0%);
    }
}





  

 
.owl-carousel .owl-item img {
    width: auto;
}




/*------ upper bar start ------*/
.upper-bar {
  background-color: #b39861;
  // height: 40px;
  padding: 10px 0;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .upper-links {
    ul {
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
    .link {
      font-family: ar-reg;
      font-size: 14px;
      color: rgb(255, 242, 213);
      margin: 0 0 0 18px;
      transition: all .5s ease-in-out;
      &:hover {
        color: rgb(15, 15, 15);
      }
    }
  }
  .upper-social {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    a {
      color: rgb(255, 242, 213);
      transition: all .5s ease-in-out;
      &:hover {
        color: rgb(15, 15, 15);
      }
    }
    .icons {
      font-size: 14px;
      i {
        margin: 0 9px;
      }
    }
    .lang-switcher {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 20px 0 0;
      color: rgb(255, 242, 213);
      font-family: ar-reg;
      font-size: 14px;
      span {
        padding: 1.4px 0 0 0;
        transition: all .5s ease-in-out;
      }
      img {
        margin: 0 7px 0 0;
        width: 22px;
        height: 13px;
      }
